button {
  z-index: 1;
  background-color: #0000;
  border: 1px solid #049dd9;
  width: 150px;
  height: 50px;
  transition: all .5s ease-in;
  display: inline-block;
  position: relative;
  overflow: hidden;
}

button:before, button:after {
  content: "";
  z-index: -1;
  width: 0;
  height: 100%;
  transition: all .5s;
  position: absolute;
  top: 0;
  overflow: hidden;
  transform: skew(15deg);
}

button:before {
  background: #0a13c2;
  left: -10px;
}

button:after {
  background: #0a13c2;
  right: -10px;
}

button:hover:before, button:hover:after {
  width: 58%;
}

button:hover span {
  color: #eff0f6;
  transition: all .3s;
}

button span {
  color: #049dd9;
  font-size: 18px;
  transition: all .3s ease-in;
}

