.stagger-visualizer {
  transform-origin: 0 0;
  width: 1100px;
  height: 550px;
  position: absolute;
}

.stagger-visualizer .dots-wrapper {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateZ(0);
}

.stagger-visualizer .dot {
  z-index: 1;
  background-color: currentColor;
  border-radius: 50%;
  width: 23px;
  height: 23px;
  margin: 16px;
  position: relative;
}

@media (width >= 740px) {
  .stagger-visualizer .dot {
    background-color: #0000;
    background-image: linear-gradient(#fff 8%, #dbf5ff 100%);
  }
}

.stagger-visualizer .cursor {
  background-color: currentColor;
  border-radius: 50%;
  width: 37px;
  height: 37px;
  margin: 9px;
  position: absolute;
  top: 0;
  left: 0;
}

